@import url(https://fonts.googleapis.com/css?family=Montserrat);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}




.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {

  /* Pick a solid background color that will be displayed while the background image is loading */
  background-color:#464646;
    
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

header {
    /* Image is centered vertically and horizontally at all times */
    background-position: center center;
    
    /* Image doesn't repeat */
    background-repeat: no-repeat;
    
    /* Makes the image fixed in the viewport so that it doesn't move when 
       the content height is greater than the image height */
    background-attachment: fixed;
    
    /* This is what makes the background image rescale based on its container's size */
    background-size: cover;
    
    /* Pick a solid background color that will be displayed while the background image is loading */
    background-color:#464646;
    
    /* SHORTHAND CSS NOTATION
     * background: url(background-photo.jpg) center center cover no-repeat fixed;
     */
  }
  
.field-container {
	 position: relative;
	 padding: 0;
	 margin: 0;
	 border: 0;
	 width: 450px;
	 height: 35px;
}
 .icons-container {
	 position: absolute;
	 top: 11px;
	 right: -25px;
	 width: 35px;
	 height: 35px;
	 overflow: hidden;
}
 .icon-close {
	 position: absolute;
	 top: 2px;
	 left: 2px;
	 width: 75%;
	 height: 75%;
	 opacity: 0;
	 cursor: pointer;
	 -webkit-transform: translateX(-200%);
	         transform: translateX(-200%);
	 border-radius: 50%;
	 -webkit-transition: opacity 0.25s ease, -webkit-transform 0.43s cubic-bezier(0.694, 0.048, 0.335, 1);
	 transition: opacity 0.25s ease, -webkit-transform 0.43s cubic-bezier(0.694, 0.048, 0.335, 1);
	 transition: opacity 0.25s ease, transform 0.43s cubic-bezier(0.694, 0.048, 0.335, 1);
	 transition: opacity 0.25s ease, transform 0.43s cubic-bezier(0.694, 0.048, 0.335, 1), -webkit-transform 0.43s cubic-bezier(0.694, 0.048, 0.335, 1);
}
 .icon-close:before {
	 content: "";
	 border-radius: 50%;
	 position: absolute;
	 top: 0;
	 left: 0;
	 width: 100%;
	 height: 100%;
	 opacity: 0;
	 border: 2px solid transparent;
	 border-top-color: #6078ea;
	 border-left-color: #6078ea;
	 border-bottom-color: #6078ea;
	 -webkit-transition: opacity 0.2s ease;
	 transition: opacity 0.2s ease;
}
 .icon-close .x-up {
	 position: relative;
	 width: 100%;
	 height: 50%;
}
 .icon-close .x-up:before {
	 content: "";
	 position: absolute;
	 bottom: 2px;
	 left: 3px;
	 width: 50%;
	 height: 2px;
	 background-color: #6078ea;
	 -webkit-transform: rotate(45deg);
	         transform: rotate(45deg);
}
 .icon-close .x-up:after {
	 content: "";
	 position: absolute;
	 bottom: 2px;
	 right: 0px;
	 width: 50%;
	 height: 2px;
	 background-color: #6078ea;
	 -webkit-transform: rotate(-45deg);
	         transform: rotate(-45deg);
}
 .icon-close .x-down {
	 position: relative;
	 width: 100%;
	 height: 50%;
}
 .icon-close .x-down:before {
	 content: "";
	 position: absolute;
	 top: 5px;
	 left: 4px;
	 width: 50%;
	 height: 2px;
	 background-color: #6078ea;
	 -webkit-transform: rotate(-45deg);
	         transform: rotate(-45deg);
}
 .icon-close .x-down:after {
	 content: "";
	 position: absolute;
	 top: 5px;
	 right: 1px;
	 width: 50%;
	 height: 2px;
	 background-color: #6078ea;
	 -webkit-transform: rotate(45deg);
	         transform: rotate(45deg);
}
 .is-type .icon-close:before {
	 opacity: 1;
	 -webkit-animation: spin 0.85s infinite;
	         animation: spin 0.85s infinite;
}
 .is-type .icon-close .x-up:before, .is-type .icon-close .x-up:after {
	 -webkit-animation: color-1 0.85s infinite;
	         animation: color-1 0.85s infinite;
}
 .is-type .icon-close .x-up:after {
	 -webkit-animation-delay: 0.3s;
	         animation-delay: 0.3s;
}
 .is-type .icon-close .x-down:before, .is-type .icon-close .x-down:after {
	 -webkit-animation: color-1 0.85s infinite;
	         animation: color-1 0.85s infinite;
}
 .is-type .icon-close .x-down:before {
	 -webkit-animation-delay: 0.2s;
	         animation-delay: 0.2s;
}
 .is-type .icon-close .x-down:after {
	 -webkit-animation-delay: 0.1s;
	         animation-delay: 0.1s;
}
 .icon-search {
	 position: relative;
	 top: 5px;
	 left: 8px;
	 width: 50%;
	 height: 50%;
	 opacity: 1;
	 border-radius: 50%;
	 border: 3px solid #c7d0f8;
	 -webkit-transition: opacity 0.25s ease, -webkit-transform 0.43s cubic-bezier(0.694, 0.048, 0.335, 1);
	 transition: opacity 0.25s ease, -webkit-transform 0.43s cubic-bezier(0.694, 0.048, 0.335, 1);
	 transition: opacity 0.25s ease, transform 0.43s cubic-bezier(0.694, 0.048, 0.335, 1);
	 transition: opacity 0.25s ease, transform 0.43s cubic-bezier(0.694, 0.048, 0.335, 1), -webkit-transform 0.43s cubic-bezier(0.694, 0.048, 0.335, 1);
}
 .icon-search:after {
	 content: "";
	 position: absolute;
	 bottom: -9px;
	 right: -2px;
	 width: 4px;
	 border-radius: 3px;
	 -webkit-transform: rotate(-45deg);
	         transform: rotate(-45deg);
	 height: 10px;
	 background-color: #c7d0f8;
}
 .field {
	 border: 0;
	 width: 100%;
	 height: 100%;
	 padding: 10px 20px;
	 background: white;
	 border-radius: 3px;
	 box-shadow: 0px 8px 15px rgba(75, 72, 72, 0.1);
	 -webkit-transition: all 0.4s ease;
	 transition: all 0.4s ease;
}
 .field:focus {
	 outline: none;
	 box-shadow: 0px 9px 20px rgba(75, 72, 72, 0.3);
}
 .field:focus + .icons-container .icon-close {
	 opacity: 1;
	 -webkit-transform: translateX(0);
	         transform: translateX(0);
}
 .field:focus + .icons-container .icon-search {
	 opacity: 0;
	 -webkit-transform: translateX(200%);
	         transform: translateX(200%);
}
 
